@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
  font-display: swap;
}
body {
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

iframe {
  border: none;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  iframe {
    height: 400px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 708px;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1220px;
    margin: 0 auto;
  }
}

picture {
  display: contents;
}

.page-body {
  min-width: 320px;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 0.88rem;
  line-height: 1.28;
  font-weight: 400;
  background-color: #ffffff;
  color: #444444;
}

.button {
  display: block;
  border: none;
  padding: 0 10px;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
}
.button--primary {
  color: #ffffff;
  background-color: #68b738;
}
.button--primary:hover {
  background-color: #5eaa2f;
}
.button--primary:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--secondary {
  color: #444444;
  background-color: #f2f2f2;
}
.button--secondary:hover {
  background-color: #ebebeb;
}
.button--secondary:active {
  color: rgba(255, 255, 255, 0.3);
}

.header {
  position: relative;
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .header {
    border: none;
  }
}

.header__container {
  display: flex;
  flex-direction: column;
  padding: 13px 0 0;
}
@media (min-width: 768px) {
  .header__container {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 25px 0 0;
  }
}
@media (min-width: 1440px) {
  .header__container {
    padding: 56px 0 0;
    justify-content: start;
  }
}

.header__wrapper {
  display: flex;
  width: 100%;
  align-items: center;
  margin: 0 0 15px;
}
@media (min-width: 768px) {
  .header__wrapper {
    width: 175px;
    margin: 0;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .header__wrapper {
    width: 202px;
  }
}

.header__link:focus {
  opacity: 0.8;
}
.header__link:hover {
  opacity: 0.8;
}
.header__link:active {
  opacity: 0.6;
}
.header__link--image {
  margin: 0 56px 0 0;
}
@media (min-width: 768px) {
  .header__link--image {
    margin: 0;
  }
}

.header__logo-image {
  display: block;
  width: 33px;
  height: 38px;
}
@media (min-width: 768px) {
  .header__logo-image {
    width: 60px;
    height: 50px;
  }
}
@media (min-width: 1440px) {
  .header__logo-image {
    width: 70px;
    height: 59px;
  }
}

.header__logo-text {
  display: block;
  width: 101px;
  height: 18px;
}
@media (min-width: 1440px) {
  .header__logo-text {
    width: 118px;
    height: 21px;
  }
}

.header__toggle {
  display: inline-block;
  padding: 0;
  margin: 0 0 0 66px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.header__toggle--closed::before {
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  mask: url("../img/sprite.svg#burger-icon") no-repeat center/contain;
  background: #000000;
}
.header__toggle--closed:focus::before {
  background: #3e3e3e;
}
.header__toggle--closed:hover::before {
  background: #3e3e3e;
}
.header__toggle--closed:active {
  opacity: 0.3;
}
.header__toggle--opened::before {
  content: "";
  display: block;
  width: 24px;
  height: 24px;
  mask: url("../img/sprite.svg#x-icon") no-repeat center/contain;
  background: #000000;
}
.header__toggle--opened:focus::before {
  background: #3e3e3e;
}
.header__toggle--opened:hover::before {
  background: #3e3e3e;
}
.header__toggle--opened:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .header__toggle {
    display: none;
  }
}

.header__wrapper--nojs .header__toggle {
  visibility: hidden;
}

.navigation {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 100%;
  width: 100%;
  background-color: #ffffff;
}
.navigation--nojs {
  position: static;
}
@media (min-width: 768px) {
  .navigation {
    position: static;
    width: 440px;
  }
}
@media (min-width: 1440px) {
  .navigation {
    width: 885px;
    margin: 0 0 0 131px;
    background-color: transparent;
  }
}

.navigation--nojs .navigation__list {
  display: block;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .navigation__list {
    border-top: 1px solid #e6e6e6;
  }
  .navigation__list--closed {
    display: none;
  }
  .navigation__list--opened {
    display: block;
  }
}
@media (min-width: 768px) {
  .navigation__list {
    display: flex;
    flex-wrap: wrap;
    gap: 33px;
  }
}
@media (min-width: 1440px) {
  .navigation__list {
    justify-content: flex-end;
  }
}

.navigation__item {
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .navigation__item {
    position: relative;
    border: none;
  }
  .navigation__item--current::before {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 2px;
    background-color: #68b738;
  }
}
@media (min-width: 1440px) {
  .navigation__item--current::before {
    bottom: -5px;
  }
  .navigation__item--current-other-color::before {
    background-color: #ffffff;
  }
}

.navigation__link {
  display: block;
  padding: 21px 0 24px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 1.25rem;
  line-height: 1;
  color: #000000;
  text-transform: uppercase;
}
.navigation__link[href]:focus {
  opacity: 0.6;
}
.navigation__link[href]:hover {
  opacity: 0.6;
}
.navigation__link[href]:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .navigation__link {
    padding: 10px 0;
    font-size: 1.13rem;
    line-height: 1.33;
  }
}
@media (min-width: 1440px) {
  .navigation__link {
    padding: 8px 0;
    font-size: 1.25rem;
    line-height: 1;
  }
  .navigation__link--other-color {
    color: #ffffff;
  }
}

.page-content__hero {
  margin: 0 0 20px;
}
@media (min-width: 768px) {
  .page-content__hero {
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .page-content__hero {
    margin: 0 0 82px;
  }
}

.page-content__program {
  margin: 0 0 23px;
}
@media (min-width: 768px) {
  .page-content__program {
    margin: 0 0 45px;
  }
}
@media (min-width: 1440px) {
  .page-content__program {
    margin: 0 0 78px;
  }
}

.page-content__program-request {
  margin: 0 0 33px;
}
@media (min-width: 768px) {
  .page-content__program-request {
    margin: 0 0 60px;
  }
}
@media (min-width: 1440px) {
  .page-content__program-request {
    margin: 0 0 80px;
  }
}

.page-content__catalog {
  margin: 0 0 25px;
}
@media (min-width: 768px) {
  .page-content__catalog {
    margin: 0 0 70px;
  }
}
@media (min-width: 1440px) {
  .page-content__catalog {
    margin: 0 0 76px;
  }
}

.page-content__extra-catalog {
  margin: 0 0 34px;
}
@media (min-width: 768px) {
  .page-content__extra-catalog {
    margin: 0 0 75px;
  }
}
@media (min-width: 1440px) {
  .page-content__extra-catalog {
    margin: 0 0 85px;
  }
}

.hero {
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  background-image: image-set(url("../img/background-cat@2x.jpg") 2x, url("../img/background-cat.jpg") 1x);
  background-repeat: no-repeat;
  background-position: top center;
  position: relative;
}
.hero::before {
  content: "";
  position: absolute;
  display: block;
  z-index: -1;
  width: 100%;
  height: 302px;
  background: linear-gradient(to right, #64a83c 50%, #62a63b 50%);
}
@media (min-width: 768px) {
  .hero {
    color: #000000;
    text-align: left;
    background-image: none;
  }
  .hero::before {
    display: none;
    background: none;
  }
}
@media (min-width: 1440px) {
  .hero::before {
    display: block;
    width: 50%;
    height: 694px;
    top: -115px;
    left: 50%;
    /* stylelint-disable */
    background-image: linear-gradient(to right, rgba(104, 183, 56, 0.85) 0, rgba(104, 183, 56, 0.85) 100%), image-set(url("../img/background-cat-desktop@2x.jpg") 2x, url("../img/background-cat-desktop.jpg") 1x);
    /* stylelint-enable */
    background-color: #403f43;
    background-repeat: no-repeat;
  }
}

.hero__container {
  padding: 26px 0 0;
}
@media (min-width: 768px) {
  .hero__container {
    padding: 65px 0 0;
    display: grid;
    grid-template-columns: 90px 1fr 90px;
  }
}
@media (min-width: 1440px) {
  .hero__container {
    padding: 110px 80px 184px 80px;
    display: block;
    position: relative;
  }
}

.hero__title {
  font-size: 2.25rem;
  line-height: 1;
  font-weight: 400;
  margin: 0 0 25px;
}
@media (min-width: 768px) {
  .hero__title {
    font-size: 3.75rem;
    margin: 0 0 40px;
    grid-column: 2/3;
  }
}
@media (min-width: 1440px) {
  .hero__title {
    grid-column: 1/2;
    grid-row: 1/2;
    width: 416px;
  }
}

.hero__subtitle {
  line-height: 1;
  text-transform: uppercase;
  margin: 0 0 29px;
}
@media (min-width: 768px) {
  .hero__subtitle {
    font-size: 1.25rem;
    margin: 0 0 52px;
    grid-column: 2/3;
    grid-row: 2/3;
  }
}

.hero__image {
  display: block;
  width: 280px;
  height: 270px;
  margin: 0 auto 3px;
}
@media (min-width: 768px) {
  .hero__image {
    width: 708px;
    height: 609px;
    grid-column: 1/4;
    grid-row: 3/5;
    margin: -32px 0 0;
  }
}
@media (min-width: 1440px) {
  .hero__image {
    margin: 0;
    position: absolute;
    width: 552px;
    height: 499px;
    top: 29px;
    right: 221px;
  }
}

.hero__button {
  font-family: inherit;
  font-size: 0.88rem;
  line-height: 1.29;
  width: 280px;
  padding: 10px;
}
@media (min-width: 768px) {
  .hero__button {
    font-size: 1.25rem;
    line-height: 1;
    grid-column: 2/3;
    grid-row: 3/4;
    padding: 16px 10px;
    width: 244px;
  }
}

.program__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
@media (min-width: 768px) {
  .program__list {
    gap: 30px;
  }
}
@media (min-width: 1440px) {
  .program__list {
    flex-direction: row;
    gap: 80px;
  }
}

.program-card {
  display: grid;
  grid-template-columns: auto 1fr;
  position: relative;
  background-color: #f2f2f2;
}
.program-card--diet {
  padding: 21px 32px 18px 20px;
}
.program-card--diet::before {
  content: "";
  display: block;
  width: 50px;
  height: 53px;
  background-image: url("../img/sprite.svg#program-diet");
  background-repeat: no-repeat;
  margin: 0 20px 0 0;
}
.program-card--weight {
  padding: 27px 33px 18px 20px;
}
.program-card--weight::before {
  content: "";
  width: 67px;
  height: 50px;
  background-image: url("../img/sprite.svg#program-weight");
  background-repeat: no-repeat;
  margin: 0 11px 0 -7px;
}
@media (min-width: 768px) {
  .program-card {
    grid-template-columns: 1fr auto;
  }
  .program-card--diet {
    padding: 80px 63px 63px 52px;
  }
  .program-card--diet::before {
    width: 200px;
    height: 211px;
    grid-row: 1/4;
    margin: 0 0 0 53px;
  }
  .program-card--weight {
    padding: 77px 29px 65px 52px;
  }
  .program-card--weight::before {
    width: 268px;
    height: 200px;
    grid-row: 1/4;
    margin: 12px 0 0 19px;
  }
}
@media (min-width: 1440px) {
  .program-card {
    width: 570px;
    grid-template-columns: auto 1fr;
  }
  .program-card--diet {
    padding: 41px 61px 59px 52px;
  }
  .program-card--diet::before {
    width: 100px;
    height: 106px;
    grid-row: 1/2;
    margin: 0 62px 0 0;
  }
  .program-card--weight {
    padding: 47px 67px 59px 52px;
  }
  .program-card--weight::before {
    width: 134px;
    height: 100px;
    grid-row: 1/2;
    margin: 0 47px 0 -19px;
  }
}

.program-card__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 1.5rem;
  line-height: 1.54;
  font-weight: 400;
  color: #000000;
  margin: 0;
  align-self: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .program-card__title {
    font-size: 2.25rem;
    line-height: 1;
    grid-column: 1/2;
    grid-row: 1/2;
  }
}
@media (min-width: 1440px) {
  .program-card__title {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}

.program-card__description {
  margin: 23px 0 18px;
  grid-column: 1/3;
}
.program-card__description::after {
  content: "";
  display: block;
  width: 240px;
  height: 1px;
  background-color: #d9d9d9;
  margin: 23px 0 0;
}
@media (min-width: 768px) {
  .program-card__description {
    font-size: 1rem;
    line-height: 1.5;
    margin: 22px 0 57px;
    grid-column: 1/2;
  }
  .program-card__description::after {
    display: none;
  }
}
@media (min-width: 1440px) {
  .program-card__description {
    margin: 39px 0 33px;
    grid-column: 1/3;
    grid-row: 2/3;
  }
}

.program-card__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 1rem;
  line-height: 1;
  color: #000000;
  text-transform: uppercase;
  grid-column: 1/3;
  display: inline-flex;
  gap: 15px;
  align-items: center;
}
.program-card__link::after {
  content: "";
  display: block;
  width: 24px;
  height: 12px;
  background-image: url("../img/sprite.svg#arrow-icon");
  background-repeat: no-repeat;
}
.program-card__link:active {
  opacity: 0.3;
}
.program-card__link:focus::after {
  width: 32px;
}
.program-card__link:hover::after {
  width: 32px;
}
@media (min-width: 768px) {
  .program-card__link {
    font-size: 1.25rem;
    grid-column: 1/2;
    gap: 25px;
  }
}
@media (min-width: 1440px) {
  .program-card__link {
    grid-column: 1/3;
  }
}

@media (min-width: 768px) and (max-width: 1439px) {
  .features {
    border-bottom: 1px solid #e6e6e6;
  }
}

.features__container {
  padding: 0 0 49px;
}
@media (min-width: 768px) {
  .features__container {
    padding: 0 79px 44px 0;
  }
}
@media (min-width: 1440px) {
  .features__container {
    padding: 0 0 66px;
  }
}

.features__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 2.25rem;
  line-height: 1.1;
  font-weight: 400;
  color: #000000;
  margin: 0 0 40px;
}
@media (min-width: 768px) {
  .features__title {
    font-size: 3.75rem;
    line-height: 1;
    margin: 0 0 32px;
  }
}

.features__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 20px;
}
@media (min-width: 768px) {
  .features__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 136px;
    row-gap: 37px;
    counter-reset: number 0;
  }
}
@media (min-width: 1440px) {
  .features__list {
    grid-template-columns: repeat(4, 250px);
    column-gap: 75px;
    row-gap: 0;
  }
}

.features__item {
  display: flex;
  width: 100%;
  align-items: center;
  gap: 20px;
}
.features__item::before {
  content: "";
  width: 60px;
  height: 60px;
  flex-shrink: 0;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: center;
}
.features__item--healthy::before {
  background-image: url("../img/sprite.svg#leaf-icon");
  background-size: 30px 32px;
}
.features__item--easy::before {
  background-image: url("../img/sprite.svg#glass-bubbles-icon");
  background-size: 26px 29px;
}
.features__item--daily::before {
  background-image: url("../img/sprite.svg#fork-knife-icon");
  background-size: 25px 30px;
}
.features__item--quickly::before {
  background-image: url("../img/sprite.svg#clock-icon");
  background-size: 26px 32px;
}
@media (min-width: 768px) {
  .features__item {
    display: block;
    min-height: 280px;
    padding: 38px 0 10px;
    counter-increment: number;
    position: relative;
  }
  .features__item::before {
    display: block;
    width: 80px;
    height: 80px;
    margin: 0 0 31px;
  }
  .features__item::after {
    content: counter(number);
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 17.5rem;
    line-height: 1;
    color: #f2f2f2;
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
  }
  .features__item--healthy::after {
    right: 22px;
  }
}

.features__item-text {
  margin: 0;
}
@media (min-width: 768px) {
  .features__item-text {
    font-size: 1rem;
    line-height: 1.5;
  }
}

