@media (min-width: 1440px) {
  .example__title {
    margin: 0 0 146px;
  }
}

.example__description {
  margin: 0 0 20px;
}
@media (min-width: 768px) {
  .example__description {
    font-size: 1rem;
    line-height: 1.5;
    margin: 0 0 51px;
  }
}
@media (min-width: 1440px) {
  .example__description {
    grid-row: 2/3;
    grid-column: 1/2;
    margin: 0 0 69px;
  }
